.backdrop {
    background: rgba($color: #000000, $alpha: 0.25);

    opacity: 1;
    position: fixed;
    top: 0;
    left: 0;

    z-index: 1000;

    width: 100%;
    height: 100%;

    // overflow-y: scroll;

    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
        visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal—movie-is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
}

.modal-movie-detail {
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translate(-50%, 0) scale(1);
    background-color: #ffffff;
    width: calc(100% - 40px);
    padding: 48px 20px 40px 20px;

    @media screen and (min-width: 320px) {
        width: 280px;
    }

    @media screen and (min-width: 768px) {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(1);
        width: 704px;

        display: flex;
        gap: 68px;
        padding: 40px 72px 40px 36px;
    }

    @media screen and (min-width: 1280px) {
        width: 806px;

        gap: 16px;
        padding: 40px 12px;
    }
}

.btn-close-modal {
    position: absolute;
    top: 4px;
    right: 4px;
    background-color: #ffffff;
    border: none;

    cursor: pointer;
}

.btn-close-modal:hover,
.btn-close-modal:focus {
    box-shadow: 0 1px 1px #0000001f, 0 4px 4px #0000000f, 1px 4px 6px #00000029;
    transform: scale(1.06);
}

.icon-close {
    display: inline-block;
    stroke-width: 0;
    stroke: currentColor;
    fill: currentColor;
}

.thumb-movie-poster {
    position: relative;

    @media screen and (min-width: 320px) {
        width: 240px;
        height: 357px;
        overflow: hidden;
    }

    @media screen and (min-width: 768px) {
        width: 264px;
        height: 374px;
    }

    @media screen and (min-width: 1280px) {
        width: 375px;
        height: 478px;
    }
}

.thumb-movie-poster>picture {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.thumb-movie-poster>picture>img {
    width: 100%;
    object-fit: contain;
    border-radius: 5px;
}

.movie-detail {
    margin-top: 20px;

    @media screen and (min-width: 768px) {
        width: 264px;
        margin-top: 0;
    }

    @media screen and (min-width: 1280px) {
        width: 391px;
    }
}

.movie-title {
    margin-top: 0;
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 20px;
    line-height: 1.15;
    text-align: left;

    color: #000000;

    @media screen and (min-width: 1280px) {
        font-size: 30px;
        line-height: 1.17;
    }
}

.list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.detail-info-movie {
    margin-top: 20px;
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
}

td {
    padding-bottom: 8px;
    vertical-align: top;
    width: 108px;

    @media screen and (min-width: 1280px) {
        width: 152px;
    }
}

.movie-detail-info {
    margin-top: 20px;
    display: flex;
    gap: 39px;

    @media screen and (min-width: 1280px) {
        gap: 83px;
    }
}

.category-item {
    color: #8c8c8c;
}

.thumb {
    min-width: 69px;
}

.vote {
    padding: 0 10px;
    color: #ffffff;
    background: #ff6b01;
    border-radius: 5px;
    font-size: 12px;
    line-height: 1.17;
}

.votes {
    background: #f7f7f7;
    border-radius: 5px;
    font-size: 12px;
    line-height: 1.17;
}

.about {
    margin-top: 20px;

    font-family: 'Roboto';
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
    text-transform: uppercase;

    color: #000000;
}

.about-desc {
    margin-top: 8px;

    font-family: 'Roboto';
    font-weight: 500;
    font-size: 12px;
    line-height: 1.67;

    color: #000000;

    // max-height: 210px;
    // overflow: scroll;
    // overflow-y: scroll;

    @media screen and (min-width: 780px) {
        // display: block;
        max-height: 210px;
        // overflow: scroll;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    @media screen and (min-width: 1280px) {
        max-height: 230px;
    }
}

.movie-btn-container {
    display: flex;
    gap: 20px;
    margin-top: 20px;

    @media screen and (min-width: 768px) {
        gap: 15px;
    }

    @media screen and (min-width: 1280px) {
        margin-left: 8px;
    }
}

.modal-btn {
    width: 110px;
    height: 44px;

    background: #ffffff;
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
    text-align: center;
    text-transform: uppercase;

    border: 1px solid #000000;
    border-radius: 5px;

    cursor: pointer;
    transition: all 1s cubic-bezier(0.4, 0, 0.2, 1);

    @media screen and (min-width: 768px) {
        width: 125px;
    }

    @media screen and (min-width: 1280px) {
        width: 136px;
    }
}

.btn-active,
.modal-btn:hover,
.modal-btn:focus {
    color: #ffffff;
    background-color: #ff6b01;
    border-color: inherit;
}